<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/drug"></BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browdrugdetail'"></BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editdrugdetail'"></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning"></Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="left" :label-width="100">
          <FormItem label="">
              <Input v-model="editFrom.createDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editFrom.drugID" :disabled="this.currentPath==='browdrugdetail'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editFrom.drugNameEN" :disabled="this.currentPath==='browdrugdetail'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editFrom.drugNameCN" :disabled="this.currentPath==='browdrugdetail'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="editFrom.drugStatus" :disabled="this.currentPath==='browdrugdetail'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editFrom.drugCategory" :disabled="this.currentPath==='browdrugdetail'" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="editFrom.LastestUpdataDate" disabled placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editFrom')" v-if="this.currentPath==='browdrugdetail'"></Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editdrugdetail'"></Button>
            <Button @click="handleSubmit('editFrom')"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          createDate:'111',
          drugID:'11',
          drugNameEN:'aaaa',
          drugNameCN:'',
          drugStatus:'11',
          drugCategory:'1111',
          LastestUpdataDate:'111'
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editdrugdetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdataModal/drug')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


